}

.line {
  fill: none;
  stroke: #fff;
  stroke-width: 9px;
  transition: stroke-dasharray .9s cubic-bezier(.4, 0, .2, 1), stroke-dashoffset .9s cubic-bezier(.4, 0, .2, 1);
}

.line1 {
  stroke-dasharray: 60 207;
  stroke-width: 9px;
}

.line2 {
  stroke-dasharray: 60 60;
  stroke-width: 9px;
}

.line3 {
  stroke-dasharray: 60 207;
  stroke-width: 9px;
}

.opened .line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134px;
  stroke-width: 9px;
}

.opened .line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30px;
  stroke-width: 9px;
}

.opened .line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134px;
  stroke-width: 9px;
}

